/* SCROLL  */

/* width */
::-webkit-scrollbar {
    width: 5px;
    
  }
  
  /* Track */
  ::-webkit-scrollbar-track {
    background: var(--bg-dark); 

  }
   
  /* Handle */
  ::-webkit-scrollbar-thumb {
    background: var(--gold); 
    
  }
  
  /* Handle on hover */
  ::-webkit-scrollbar-thumb:hover {
    background: var(--gold); 
  }
  
  @media(max-width: 768px){
    ::-webkit-scrollbar {
        width: 3px;
        height: 0px;
        
      }
  }
